import React, { useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';

function RadarChart({ score }) {
    const chartRef = useRef();

    useEffect(() => {
        const ctx = chartRef.current.getContext('2d');

        new Chart(ctx, {
            type: 'radar',
            data: {
                labels: ['정서적 안정', '심리적 안정', '자존감', '회복 탄력성', '대인 관계'], // Use the keys of the score object as labels
                datasets: [{
                    label: '내 점수',
                    fontColor: '#fff',
                    data: Object.values(score),
                    backgroundColor: 'rgba(0, 0, 255, 0.2)',
                    borderColor: 'blue',
                },
                {
                    label: '표준 점수',
                    data: [63, 60, 63, 50, 50],
                    backgroundColor: 'rgba(255, 0, 0, 0.2)',
                    borderColor: 'red',
                }]
            },
            options: {
                scales: {
                    r: {
                        min: 0,
                        max: 100,
                        ticks: {
                            stepSize: 20
                        }
                    }
                },
                elements:{
                    line:{
                        borderWidth: 0
                    },
                    point:{
                        pointBorderWidth: 0,
                        pointHitRadius: 0,
                        pointRadius: 0,
                    }
                },
                plugins: {
                    legend: {
                        display: true,
                        position: 'top',
                        labels: {
                            // Change the name of the label
                            text: 'Custom Label Name'
                        }
                    }
                }
            }
        });
    }, [score]);

    return <canvas id="1" ref={chartRef} width={400} height={400} style={{width: '400px', height: '400px', backgroundColor:'white', borderRadius: '10px', paddingBottom: '20px'}}/>;
}

export default RadarChart;