
import React, { useState, useEffect } from 'react';
import Wrapper from '../common/Wrapper';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import result from '../../pages/contents/result';
import styled from 'styled-components';
import LoadingBar from '../common/LoadingBar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTwitter, faFacebook } from '@fortawesome/free-brands-svg-icons';
import { faLink } from '@fortawesome/free-solid-svg-icons';
import '../common/Main.css';
import Footer from '../common/Footer';
import $ from 'jquery';
import RadarChart from './Radarchart';
import Button from '../common/Button';

const Result = styled.div`
  text-align : center;
  margin : 0;
  padding : 0;
  li {
    margin-top : 7px;
  }
`;

const useStyles = makeStyles({
    title: {
        fontSize: 40,
        marginBottom: '10px',
        marginTop: '10px',
        textAlign: "center",
        fontFamily: 'TTTogether',
        color: '#5887ff',
    },
    name: {
        fontSize: 30,
        marginBottom: '10px',
        marginTop: '-10px',
        textAlign: "center",
        color: 'black',
        fontFamily: 'TTTogether',
        fontWeight: '400',
    },
    button: {
        textAlign: "center",
        fontFamily: 'Y_Spotlight',
        marginTop: '5%',
    },
});

const Img = styled.img`
    width : 200px;
    height : 200px;
    border-radius : 50%;
    border : 1px solid white;
  `;

const Textbox = styled.div`
    padding : 30px;
    background-color : white;
    border-radius: 10px;
    margin-bottom : 30px;
  `;
const Subtitle = styled.div`
    color : black;
    font-family: 'TTTogether';
    font-size : 1.5rem;
    margin-bottom : 10px;
    text-align : left;
  
  `
const Text = styled.div`
  color : black;
  text-align : left;
  font-family: 'IBMPlexSansKR-Regular';
`;
const Share = styled.div`
    font-size : 1.2em;
    color : #b5bbe6;
    margin-left: 5%;
  `;
const ShareBox = styled.div`
    display: flex;  
    margin: 5%;
    button {
        display: flex;
        width: 100%;
        background:none;
        outline: none;
        border: none;
    }
    textarea {
        opacity: 0;
    }
  `;

  const Notice = styled.div`
    display : none;
    position : fixed;
    background : rgb(0,0,0, 0.8);
    color : white;
    z-index : 1000;
    width : 300px;
    height : 50px;
    align-items : center;
    justify-content : center;
    border-radius : 6px;
  `;

const Resultcard = (data) => {
    const [omgsrc, setSrc] = useState('');
    const [showResult, setShowResult] = useState(false);
    const classes = useStyles();
    const [total, setTotal] = useState(0);
    const total_score = {
        'depression_score' : 100 - Math.ceil(data.data.depression * 3.70),
        'anxiety_score' : 100 - Math.ceil(data.data.anxiety * 4.76),
        'esteem_score' : 100 - Math.ceil(data.data.esteem * 3.3),
        'resilience_score' : Math.ceil(data.data.resilience * 2.5),
        'icq_score' : Math.ceil(data.data.icq * 1.66)    
    }
    
    
    useEffect(() => {
        //maxOmg 에 따른 index 설정
        const newLocal = require(`../../img/brain.png`);        //img source 설정 //나중에 그래프로 대체
        setSrc(newLocal.default);
        const tick = setTimeout(() => {
            setShowResult(true);
        }, 3000);
        return () => clearTimeout(tick);
    }, []);

    const copyUrlRef = React.useRef();

    const copyUrl = (e) => {
        if(!document.queryCommandSupported("copy")){
            return alert("복사 기능이 지원 되지 않는 브라우저입니다.");
        } else{
            copyUrlRef.current.select();
            document.execCommand('copy');
            e.target.focus();
            //링크 복사 함수
            $(".show").css("display", "flex");
            const time = setTimeout(() => {
                $(".show").css("display", "none");
            }, 1000);
            return () => clearTimeout(time);
            
        }
    } 
    const goMentalPt = () => {
        window.open('http://mentalpt.co.kr/');
    }
    const shareTwitter = () => {
        window.open("https://twitter.com/intent/tweet"
            + "?via=likeCpp"
            + "&text=" + encodeURIComponent("재미로 보는 나와 비슷한 오마이걸 멤버는?!") // twitter 공유 함수
            + "&url=" + encodeURIComponent(window.location.href)
        );

    };    
    const shareFacebook = () => {
        window.open('https://www.facebook.com/sharer/sharer.php'    //facebook 공유 함수 
            + "?u=" + encodeURIComponent(window.location.href)
        );
    };

    function DepressionMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score <= 4) {
            level = 'None-minimal';
        } else if (score >= 5 && score <= 9) {
            level = 'mild';
        } else if (score >= 10 && score <= 14) {
            level = 'moderate';
        } else if (score >= 15 && score <= 19) {
            level = 'moderately severe';
        } else {
            level = 'severe';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.depression.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>정서적 안정 ({total_score.depression_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }

    function AnxietyMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score <= 4) {
            level = 'None-minimal';
        } else if (score >= 5 && score <= 9) {
            level = 'mild';
        } else if (score >= 10 && score <= 14) {
            level = 'moderate';
        } else {
            level = 'severe';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.anxiety.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>심리적 안정 ({total_score.anxiety_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }
    function SelfEsteemMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score <= 25) {
            level = 'low';
        } else if (score >= 26 && score < 30) {
            level = 'medium';
        } else {
            level = 'high';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.esteem.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>자존감 ({total_score.esteem_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }
    function ResilienceMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score < 20) {
            level = 'low';
        } else if (score >= 20 && score < 30) {
            level = 'medium';
        } else {
            level = 'high';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.resilience.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>탄력성 ({total_score.resilience_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }
    function ICQMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score < 30) {
            level = 'low';
        } else if (score >= 30 && score < 45) {
            level = 'medium';
        } else {
            level = 'high';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.icq.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>대인관계 ({total_score.icq_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }

    return (
        <>
        <Wrapper>
            {showResult &&
                <Result style={{backgroundColor: 'white'}}> 
                    <Typography className={classes.title}>나의 멘탈 점수</Typography>
                    <Typography className={classes.name}>종합 {Math.ceil(total_score.anxiety_score + total_score.depression_score + total_score.esteem_score + total_score.icq_score + total_score.resilience_score) / 5}점</Typography>
                    <RadarChart score={total_score}/>
                    <DepressionMessage score={data.data.depression}/>
                    <AnxietyMessage score={data.data.anxiety}/>
                    <SelfEsteemMessage score={data.data.esteem}/>
                    <ResilienceMessage score={data.data.resilience}/>
                    <ICQMessage score={data.data.icq}/>
                    <Typography className={classes.name}>멘탈 점수를 높이고 싶다면!</Typography>
                    <Button onClick={goMentalPt}  style = {{background : "#407aff", margin: "30px 0"}}> 멘탈 트레이닝 하기</Button>
                    {/* <ShareBox>
                        <button onClick={copyUrl} >
                            <FontAwesomeIcon icon={faLink} size="2x" color="white" /><Share>링크 복사</Share>
                            <form>
                                <textarea ref={copyUrlRef} defaultValue={window.location.href} readOnly />
                            </form>
                        </button>
                    </ShareBox> */}
                </Result>
            }
            {!showResult &&
                <LoadingBar done={100} />
            }
                <Notice className = "show">클립보드에 링크가 복사되었습니다.</Notice>
        </Wrapper>
        {showResult && <Footer />}
        </>
    );
};

export default Resultcard;